import React, { useState } from 'react';
import './sortModal.scss';
import sort from '../../assets/images/Sort/sort_by.svg';
import categorySort from '../../assets/images/Sort/categories_sort.svg';
import close from '../../assets/images/Close.svg';
import { useSelector } from 'react-redux';
import ReactModal from 'react-modal';

export function SortModal({ isOpen, changeModalState, changeCategories }) {
  const [mainButtons, setButtons] = useState('sort');
  const tags = useSelector((state) => state.tags);

  const [selectedTags, setSelectedTags] = useState([]);

  function changeTags(name) {
    selectedTags.includes(name)
      ? setSelectedTags(selectedTags.filter((item) => item !== name))
      : setSelectedTags([...selectedTags, name]);
  }

  function changeButton(id) {
    setButtons(id);
  }
  console.log(changeModalState)
  const customStyles = {
    overlay: {
      zIndex: 15,
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
    },
  };

  return (
    <ReactModal
      isOpen={isOpen}
      // onRequestClose={changeModal}
      style={customStyles}
      className="sort-modal-window">
      <div className="sort-modal-window__close">
        <img src={close} alt="" onClick={() => changeModalState()} />
      </div>
      <h3 className="sort-modal-window__title">sort</h3>
      <ul className="sort-modal-window__list">
        <li
          className={`sort-modal-window__list__item ${
            mainButtons === 'sort' && 'active'
          }`}
          onClick={() => changeButton('sort')}>
          <img src={sort} alt="" />
          Sort by:
        </li>
        <li
          className={`sort-modal-window__list__item ${
            mainButtons === 'category' && 'active'
          }`}
          onClick={() => changeButton('category')}>
          <img src={categorySort} alt="" />
          Category:
        </li>
      </ul>
      <ul
        className={`sort-modal-window__sort-list_sort ${
          mainButtons === 'sort' && 'active'
        }`}>
        <li
          className="sort-modal-window__sort-list__item"
          onClick={() => changeTags('Most votes')}>
          <span className={!selectedTags.includes('Most votes') || 'active'}>
            Most votes
          </span>
        </li>
        <li
          className="sort-modal-window__sort-list__item"
          onClick={() => changeTags('Recently added')}>
          <span
            className={!selectedTags.includes('Recently added') || 'active'}>
            Recently added
          </span>
        </li>
      </ul>
      <ul
        className={`sort-modal-window__sort-list_category ${
          mainButtons === 'category' && 'active'
        }`}>
        {tags.map((item, index) => {
          return (
            <li
              className="sort-modal-window__sort-list__item"
              key={index}
              onClick={() => changeTags(item.name)}>
              <span className={!selectedTags.includes(item.name) || 'active'}>
                {item.name}
              </span>
            </li>
          );
        })}
      </ul>
      <button
        className="sort-modal-window__button"
        onClick={() => {
          changeCategories(selectedTags);
          changeModalState();
        }}>
        Apply
      </button>
    </ReactModal>
  );
}
