import { useState } from 'react';
import { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import ReactModal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import { emailRegExp } from '../../features/regexps';
import {
  resetError,
  resetPassword,
  unsetResetPasswordEmail,
} from '../../redux/actions';
import { CheckEmailModal } from './checkEmailModal';

const LOCAL_ERRORS = ['auth/user-not-found'];

export function ResetPasswordModal({
  isOpen,
  onClose,
  onOpen,
  onLoginModalOpen,
}) {
  const dispatch = useDispatch();
  const { register, handleSubmit, reset } = useForm();
  const [isCheckEmailModalOpen, setCheckEmailModalOpen] = useState(false);
  const [isEmailIncorrect, setEmailIncorrect] = useState(false);
  const [wasTriedSubmitted, setWasTriedSubmitted] = useState(false);
  const onCheckEmailModalClose = () => setCheckEmailModalOpen(false);
  const onCheckEmailModalOpen = () => setCheckEmailModalOpen(true);
  const onModalClose = () => {
    onClose();
    reset();
    setWasTriedSubmitted(false);
    setEmailIncorrect(false);
    if (LOCAL_ERRORS.includes(error?.code)) dispatch(resetError());
  };
  const emailRegister = register('email', {
    pattern: emailRegExp,
    required: true,
  });
  const customStyles = {
    overlay: {
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
      position: 'fixed',
      height: '100%',
      width: '100%',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'flex-start',
      overflowY: 'scroll',
    },
    content: {
      minWidth: '655px',
      padding: '58px 70px',
      backgroundColor: '#272A33',
      borderRadius: '23px',
      textAlign: 'center',
      outline: 'none',
      position: 'relative',
      zIndex: 0,
      margin: 'auto 0',
    },
  };
  const backToLoginModal = () => {
    onModalClose();
    onLoginModalOpen();
  };
  const onSubmit = (data) => {
    console.log(data);
    dispatch(resetPassword(data.email));
  };
  const onErrorSubmit = (errors) => {
    console.log(errors);
    setWasTriedSubmitted(true);
    setEmailIncorrect(true);
  };
  const error = useSelector((state) => state.error.error);
  const email = useSelector((state) => state.resetEmailPassword);
  useEffect(() => {
    if (LOCAL_ERRORS.includes(error?.code)) {
      setEmailIncorrect(true);
      setWasTriedSubmitted(true);
    } else {
      setEmailIncorrect(false);
      setWasTriedSubmitted(false);
    }
    if (email && !error) {
      onModalClose();
      onCheckEmailModalOpen();
    }
  }, [error, email]);

  return (
    <>
      <CheckEmailModal
        onClose={onCheckEmailModalClose}
        isOpen={isCheckEmailModalOpen}
        email={email}
        onLoginModalOpen={onLoginModalOpen}
        onResetPasswordModelOpen={onOpen}
      />
      <ReactModal
        isOpen={isOpen}
        onRequestClose={onModalClose}
        style={customStyles}
        className="authorization-modal">
        {
          <article className="authorization-modal-content">
            <div className="authorization-modal__right">
              <p
                className="authorization-modal__back"
                onClick={backToLoginModal}>
                Back
              </p>
            </div>
            <div style={{ textAlign: 'left' }}>
              <h2 className="authorization-modal__title">Reset password</h2>
            </div>
            <p className="authorization-modal__info">
              Enter the email associated with your account and we'll send
              instructions to reset your password.
            </p>
            <form
              className="authorization-modal__form"
              onSubmit={handleSubmit(onSubmit, onErrorSubmit)}>
              <div className="authorization-modal__text-input">
                <label className="authorization-modal__label" htmlFor="email">
                  Email Address
                </label>
                <div
                  className={`authorization-modal__input-wrapper ${
                    isEmailIncorrect &&
                    'authorization-modal__input-wrapper--with-error'
                  }`}>
                  <input
                    className={`authorization-modal__input ${
                      isEmailIncorrect && 'authorization-modal__input--invalid'
                    }`}
                    type="text"
                    name="email"
                    id="email"
                    placeholder="Enter your email"
                    {...emailRegister}
                    onChange={(event) => {
                      emailRegister.onChange(event);
                      setEmailIncorrect(
                        !emailRegExp.test(event.target.value) &&
                          wasTriedSubmitted
                      );
                    }}
                  />
                </div>
                {isEmailIncorrect && (
                  <label
                    className="authorization-modal__label authorization-modal__label--error"
                    htmlFor="email">
                    Sorry, this email can't be found. Let's try another one.
                  </label>
                )}
              </div>
              <button
                type="submit"
                className="authorization-modal__btn authorization-modal__btn--gradient authorization-modal__btn--with-shadow"
                style={{ marginTop: '22px' }}>
                Continue
              </button>
            </form>
          </article>
        }
      </ReactModal>
    </>
  );
}
