import React from 'react';
import ReactModal from 'react-modal';

export function SuggestModal({ isOpen, onClose }) {
  if (!isOpen) return null;
  const customStyles = {
    overlay: {
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
      position: 'fixed',
      bottom: 0,
      right: 0,
      top: 0,
      left: 0,
    },
    content: {
      maxWidth: '585px',
      height: 335,
      padding: '33px 30px',
      backgroundColor: '#272A33',
      borderRadius: '23px',
      textAlign: 'center',
      position: 'absolute',
      border: 'none',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      display: 'flex',
      flexDirection: 'column',
    },
  };
  return (
    <ReactModal isOpen={isOpen} onRequestClose={onClose} style={customStyles}>
      <aside className="suggest__modal">
        <span className="suggest__modal-close" onClick={onClose}></span>
        <div className="suggest__modal-content">
          <h2 className="suggest__modal-title">Thank you!</h2>
          <div className="suggest__modal-img"></div>
          <p className="suggest__modal-text">
            Received! We will review your suggestion ASAP
          </p>
        </div>
      </aside>
    </ReactModal>
  );
}
