import { useEffect } from 'react';
import ReactModal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import notificationIcon from '../../assets/images/notification-icon.png';
import { resetError, unsetResetPasswordEmail } from '../../redux/actions';

export function CheckEmailModal({
  isOpen,
  onClose,
  email,
  onLoginModalOpen,
  onResetPasswordModelOpen,
}) {
  const customStyles = {
    overlay: {
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
      position: 'fixed',
      height: '100%',
      width: '100%',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'flex-start',
      overflowY: 'scroll',
    },
    content: {
      minWidth: '655px',
      padding: '58px 70px',
      backgroundColor: '#272A33',
      borderRadius: '23px',
      textAlign: 'center',
      outline: 'none',
      position: 'relative',
      zIndex: 0,
      margin: 'auto 0',
    },
  };
  const dispatch = useDispatch();
  const goToResetPasswordModel = () => {
    onResetPasswordModelOpen();
    onClose();
    dispatch(unsetResetPasswordEmail());
  };
  const onModalClose = () => {
    onClose();
    dispatch(unsetResetPasswordEmail());
  };
  return (
    <ReactModal
      isOpen={isOpen}
      onRequestClose={onModalClose}
      style={customStyles}
      className="authorization-modal">
      {
        <article className="authorization-modal-content">
          <div className="authorization-modal__right">
            <p
              className="authorization-modal__back"
              onClick={goToResetPasswordModel}>
              Back
            </p>
          </div>
          <header style={{ textAlign: 'left' }}>
            <h2 className="authorization-modal__title">Check your email</h2>
          </header>
          <div className="authorization-modal__notification">
            <img
              className="authorization-modal__img"
              src={notificationIcon}
              alt="notification icon"
            />
          </div>
          <p className="authorization-modal__info">
            Weve sent password reset instruction to:{' '}
            <span className="authorization-modal__email">{email}</span>
          </p>
          <p className="authorization-modal__info">
            If it doesnt arrive soon, check your spam folder or{' '}
            <span className="authorization-modal__link">
              send the email again.
            </span>
          </p>
          {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
          <p className="authorization-modal__info">
            Need help?{' '}
            <a className="authorization-modal__link">Contact support</a>
          </p>
          {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
          <footer className="authorization-modal__left">
            <a
              className="authorization-modal__link"
              onClick={() => {
                onLoginModalOpen();
                onClose();
              }}>
              Back to login
            </a>
          </footer>
        </article>
      }
    </ReactModal>
  );
}
