import React from 'react';
import './registration-styles.scss';
import leftArrow from '../../assets/images/arrow-left-icon.svg';
import { NavLink, Route } from 'react-router-dom';
import { RegistrationForm } from './RegistrationForm/RegistrationForm'
import { RegistrationDescripModal } from './RegistrationDescripModal/RegistrationDescripModal';
import { useState } from 'react';
export function Registration() {

  const [open, setOpen] = useState(true);

  const handleOnClose = () => setOpen(false)

  return (
    <section className="registration-cause">
      <div className="registration-cause__nav">
        <NavLink to={'/'} exact>
          <div className="registration-cause__nav-icon">
            <img src={leftArrow} alt="Arrow back" />
          </div>
        </NavLink>
        <h2 className="registration-cause__nav-title">Register a Cause</h2>
      </div>
      <div className="registration-cause__content">
        <Route
          path={'/register/register-form'}
          exact
          component={RegistrationForm}
        />
      </div>
      <RegistrationDescripModal isOpen={open} onClose={handleOnClose}  />
    </section>
  );
}
