import React, { useState } from 'react';
import faq from '../../../assets/images/helpCenterFaqMan.svg';
import { NavLink } from 'react-router-dom';
import arrow from '../../../assets/images/helpCenterFaqArrowBottom.svg';
import back from '../../../assets/images/Back.svg';
import './faqPageHelpCenter.scss';

export function FaqPageHelpCenter() {
  const [faqQuestions, setFaqQuestions] = useState([
    {
      id: 0,
      name: 'Can I upgrade later on?',
      descriptions:
        "As long as your current affiliate earning are over $100, you'll be paid roughly every 45 days. If you haven't earned $100 since your last payment.",
      isActive: false,
    },
    {
      id: 1,
      name: "What's the real cost?",
      descriptions:
        "As long as your current affiliate earning are over $100, you'll be paid roughly every 45 days. If you haven't earned $100 since your last payment.",
      isActive: true,
    },
    {
      id: 2,
      name: 'Can I upgrade later on?',
      descriptions:
        "As long as your current affiliate earning are over $100, you'll be paid roughly every 45 days. If you haven't earned $100 since your last payment.",
      isActive: false,
    },
    {
      id: 3,
      name: 'Can I upgrade later on?',
      descriptions:
        "As long as your current affiliate earning are over $100, you'll be paid roughly every 45 days. If you haven't earned $100 since your last payment.",
      isActive: false,
    },
    {
      id: 4,
      name: 'Can I upgrade later on?',
      descriptions:
        "As long as your current affiliate earning are over $100, you'll be paid roughly every 45 days. If you haven't earned $100 since your last payment.",
      isActive: false,
    },
    {
      id: 5,
      name: 'Can I upgrade later on?',
      descriptions:
        "As long as your current affiliate earning are over $100, you'll be paid roughly every 45 days. If you haven't earned $100 since your last payment.",
      isActive: false,
    },
  ]);
  const changeFaqQuestions = (id) =>
    setFaqQuestions(
      faqQuestions.map((element) =>
        element.id === id
          ? { ...element, isActive: !element.isActive }
          : element
      )
    );

  return (
    <section className="help-center-faq">
      <div className="help-center-faq__title">
        <div className="help-center-faq__title__back">
          <NavLink to={'/mainPageHelpCenter'}>
            <img src={back} />
          </NavLink>
        </div>
        <div className="help-center-faq__title__content">
          <img src={faq} />
          <h3 className="help-center-faq__title__content__logo">FAQs</h3>
          <h4 className="help-center-faq__title__content__second">
            Hello! How can we help?
          </h4>
        </div>
      </div>
      <div className="help-center-faq__accordion">
        <h4>Frequently asked questions :</h4>
        <ul className="help-center-faq__accordion__list">
          {faqQuestions.map((questions) => {
            return (
              <li
                key={questions.id}
                className={`help-center-faq__accordion__list__item ${
                  questions.isActive && 'active'
                }`}
                onClick={() => changeFaqQuestions(questions.id)}>
                <div className="help-center-faq__accordion__list__item__title">
                  <h5>{questions.name}</h5>
                  <img src={arrow} alt="" />
                </div>
                <div
                  className={`help-center-faq__accordion__list__item__description`}>
                  {questions.descriptions}
                </div>
              </li>
            );
          })}
        </ul>
      </div>
    </section>
  );
}
