import React from 'react';
import './suggest-styles.scss';
import leftArrow from '../../assets/images/arrow-left-icon.svg';
import { NavLink, Route } from 'react-router-dom';
import { SuggestForm } from './SuggestForm/SuggestForm';
import { SuggestDescriptionModal } from './SuggestDescripModal/SuggestDescriptionModal';
import { useState } from 'react';

export function Suggest() {
  
  const [open, setOpen] = useState(true);

  const handleOnClose = () => setOpen(false);

  return (
    <section className="suggest-cause">
      <nav className="suggest-cause__nav">
        <NavLink to={'/'} exact>
          <div className="suggest-cause__nav-icon">
            <img src={leftArrow} alt="Arrow back" />
          </div>
        </NavLink>
        <h2 className="suggest-cause__nav-title">Suggest a Cause</h2>
      </nav>
      <div className="suggest-cause__content">
        <Route path={'/suggest/suggest-form'} exact component={SuggestForm} />
      </div>
      <SuggestDescriptionModal isOpen={open} onClose={handleOnClose} />
    </section>
  );
}
