import React from 'react';
import ReactModal from 'react-modal';
import './error-modal.scss';

export function ErrorModal({ isOpen, onClose }) {
  const handleClick = () => document.location.reload();

  const customStyles = {
    overlay: {
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
      position: 'fixed',
      height: '100%',
      width: '100%',
      top: 0,
      left: 0,
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
    content: {
      minWidth: '585px',
      padding: '33px 30px',
      backgroundColor: '#272A33',
      borderRadius: '23px',
      textAlign: 'center',
    },
  };
  return (
    <ReactModal
      isOpen={isOpen}
      onRequestClose={onClose}
      style={customStyles}
      className="error__modal">
      <span className="error__modal-close" onClick={onClose}></span>
      <div className="error__modal-content">
        <h2 className="error__modal-title">No Internet Connection</h2>
        <div className="error__modal-img"></div>
        <p className="error__modal-text">
          Your internet connection is currently not available, please check or
          try again.
        </p>
        <button className="error__modal-reload" onClick={handleClick}>
          Try Again
        </button>
      </div>
    </ReactModal>
  );
}
