import React from 'react';
import { useState } from 'react';
import { SuggestModal } from '../SuggestModal/SuggestModal';
import arrowDown from '../../../assets/images/arrow-down-icon.svg';
import Select from 'react-select';
import { useForm } from 'react-hook-form';
import countries from 'countries-list';

export function SuggestForm() {
  const { register, handleSubmit, setValue } = useForm();
  const countriesOptions = Object.entries(countries.countries).map(country=>({value: country[0], label: country[1].name}))
  const onSubmit = (d) => {
    console.log(d);
  };
  const onErrorSubmit = (errors) => {
    for (const [key, value] of Object.entries(errors)) {
      value.ref.parentElement.classList.add(
        'suggest-form-page__content-inputs__required'
      );
    }
  };
  const requiredInputChangeHandler = (event) => {
    if (!event.target.value)
      event.target.parentElement.classList.add(
        'suggest-form-page__content-inputs__required'
      );
    else
      event.target.parentElement.classList.remove(
        'suggest-form-page__content-inputs__required'
      );
    console.log(event.target.parentElement);
  };
  const nameRegister = { ...register('sg_name', { required: true }) };
  const siteRegister = register('sg_website', { required: true });
  const [open, setOpen] = useState(false);
  register('sg_country');

  const customStyles = {
    option: (provider, state) => ({
      ...provider,
      padding: '16px 0 12px 24px',
      borderRadius: 10,
      fontSize: 16,
      lineHeigth: 28,
      fontWeight: state.isSelected || state.isFocused ? 700 : 500,
      color: 'black',
      backgroundColor:
        state.isSelected || state.isFocused ? '#ccc' : 'transparent',
    }),
    valueContainer: (provider) => ({
      ...provider,
      background: '#f5f5f5',
      padding: '18px 25px',
      fontSize: '16px',
      lineHeight: '28px',
      fontWeight: '500',
      borderRadius: 10,
    }),
    menu: (provider) => ({
      ...provider,
      backgroundColor: '#f5f5f5',
      border: '1px solid #e6e6e6',
      boxShadow: '0 0.125rem 0.25rem rgb(0 0 0 / 8%)',
      borderRadius: 10,
    }),
    indicatorsContainer: (provider) => ({
      ...provider,
      img: arrowDown,
    }),
    indicatorSeparator: () => ({
      display: 'none',
    }),
    control: (provider, state) => ({
      ...provider,
      background: '#f5f5f5',
      border: state.isSelected ? '1px solid #e6e6e6' : '1px solid #e6e6e6',
      borderRadius: '10px',
      marginBottom: '12px',
    }),
    menuPortal: (provider) => ({
      ...provider,
      color: '#e6e6e6',
    }),
  };
  return (
    <section className="suggest-form-page">
      <div className="suggest-form-page__wrapper">
        <div className="suggest-form-page__content">
          <aside className="suggest-form-page__content-tab">
            <p>Details</p>
          </aside>
          <form
            className="suggest-form-page__content-inputs"
            onSubmit={handleSubmit(onSubmit, onErrorSubmit)}>
            <label htmlFor="sg_name">
              <input
                type="text"
                name="suggest_name"
                id="sg_name"
                placeholder="Name of cause*"
                {...nameRegister}
                onChange={(event) => {
                  nameRegister.onChange(event);
                  requiredInputChangeHandler(event);
                }}
              />
            </label>
            <label htmlFor="sg_typeOfCause">
              <input
                type="text"
                name="suggest_typeOfCause"
                id="sg_typeOfCause"
                placeholder="Type Of Cause"
                {...register('sg_typeOfCause')}
              />
            </label>
            <Select
              placeholder="Location"
              styles={customStyles}
              options={countriesOptions}
              onChange={(e) => setValue('sg_country', e.value)}
            />
            <label htmlFor="sg_website">
              <input
                type="text"
                name="suggest_website"
                id="sg_website"
                placeholder="Website Link/Social Media Link*"
                {...siteRegister}
                onChange={(event) => {
                  siteRegister.onChange(event);
                  requiredInputChangeHandler(event);
                }}
              />
            </label>
            <label htmlFor="sg_email">
              <input
                type="text"
                name="suggest_email"
                id="sg_email"
                placeholder="Email"
                {...register('sg_email')}
              />
            </label>
            <label htmlFor="sg_additionalInfo">
              <input
                type="text"
                name="suggest_additionalInfo"
                id="sg_additionalInfo"
                placeholder="Additional Info"
                {...register('sg_additionalInfo')}
              />
            </label>
            <button type="submit" style={{ display: 'none' }}></button>
          </form>
        </div>
      </div>
      <button
        className="suggest-form-page__send-btn"
        onClick={() => setOpen(true)}>
        Send
      </button>
      <SuggestModal isOpen={open} onClose={() => setOpen(false)} />
    </section>
  );
}
