import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import back from '../../../assets/images/Back.svg';
import feedbackMan from '../../../assets/images/feedbackHeplCenter.svg';
import starNotSelected from '../../../assets/images/FeedbackStarNotSelected.svg';
import starSelected from '../../../assets/images/FeedbackStarSelected.svg';
import speaker from '../../../assets/images/feedback_speaker.svg';
import creditCard from '../../../assets/images/feedback_credit_card.svg';
import lightning from '../../../assets/images/feedback_lightning.svg';
import post from '../../../assets/images/feedback_post.svg';
import phone from '../../../assets/images/feedback_phone.svg';
import './feedbackPageHelpCenter.scss';

export function FeedbackPageHelpCenter() {
  const feedBackForm = {
    rating: 0,
    theme: [],
    comment: '',
  };
  const [rating, setRating] = useState();
  const stars = (attr) => {
    feedBackForm.rating = attr;
    const listStar = document.querySelector(
      '.help-center-feedback__form__star__list'
    ).children;
    for (let i = 0; i <= attr; i++) {
      listStar[i].classList.add('active');
    }
  };
  return (
    <section className="help-center-faq">
      <div className="help-center-faq__title">
        <div className="help-center-faq__title__back">
          <NavLink to={'/mainPageHelpCenter'}>
            <img src={back} />
          </NavLink>
        </div>
        <div className="help-center-faq__title__content">
          <img src={feedbackMan} />
          <h3 className="help-center-faq__title__content__logo">
            Give feedback
          </h3>
          <h4 className="help-center-faq__title__content__second">
            Rate your Experience
          </h4>
        </div>
      </div>
      <form action="" className="help-center-feedback__form">
        <h3>Are you satisfied with Ouno ?</h3>
        <ul className={'help-center-feedback__form__star__list'}>
          <li onClick={() => stars(0)}>
            <img src={starNotSelected} alt="" />
          </li>
          <li onClick={() => stars(1)}>
            <img src={starNotSelected} alt="" />
          </li>
          <li onClick={() => stars(2)}>
            <img src={starNotSelected} alt="" />
          </li>
          <li onClick={() => stars(3)}>
            <img src={starNotSelected} alt="" />
          </li>
          <li onClick={() => stars(4)}>
            <img src={starNotSelected} alt="" />
          </li>
        </ul>
        <h3>Tell us what can be improved ?</h3>
        <ul className={'help-center-feedback__form__improved__list'}>
          <li className={'help-center-feedback__form__improved__list__item'}>
            <span>
              <img src={post} alt="" />
              Lorem
            </span>
          </li>
          <li className={'help-center-feedback__form__improved__list__item'}>
            <span>
              <img src={lightning} alt="" />
              Speed or Efficiency
            </span>
          </li>
          <li className={'help-center-feedback__form__improved__list__item'}>
            <span>
              <img src={phone} alt="" />
              Customer Support
            </span>
          </li>
          <li className={'help-center-feedback__form__improved__list__item'}>
            <span>
              <img src={speaker} alt="" />
              Other
            </span>
          </li>
          <li className={'help-center-feedback__form__improved__list__item'}>
            <span>Overall Service</span>
          </li>
          <li className={'help-center-feedback__form__improved__list__item'}>
            <span>
              <img src={creditCard} alt="" />
              Payment
            </span>
          </li>
        </ul>
        <h3 style={{ paddingBottom: '20px' }}>
          Describe it in more detail please:
        </h3>
        <textarea
          name=""
          id=""
          cols="30"
          rows="10"
          placeholder="Write comment..."></textarea>
        <button className={'help-center-feedback__form__btn'}>submit</button>
      </form>
    </section>
  );
}
