import {
  GET_CAUSES,
  GET_COUNTDOWN,
  GET_LEADERBOARDDATA,
  GET_COUNTRIES,
  GET_PASTDRAWLIST,
  GET_TAGS,
  CREATE_CAUSE,
  GET_CATEGORIES,
  LOGIN_USER,
  HIDE_ERROR,
  SET_EMAIL_RESET_PASSWORD,
  UNSET_EMAIL_RESET_PASSWORD,
  SET_VERIFY_EMAIL,
  UNSET_VERIFY_EMAIL,
} from './types';

const initialState = {
  causes: [],
  tags: [
    {
      additional_image_urls: [
        'https://storage.googleapis.com/ouno-app.appspot.com/cause_images/rc-upload-1628848656505-3.png',
      ],
      additional_images: ['cause_images/rc-upload-1628848656505-3.png'],
      address: '',
      category_id: 'yF9ywJqhzEYmwSZWZZo1',
      country: 'AU',
      created: { seconds: 1626878854, nanoseconds: 426757000 },
      description:
        'In desperate need for some new wheel bearings\n\nTest\n\nTest\n\nTest\n\nCheck out my jandals',
      display_name: 'Dad train',
      facebook: '',
      hidden: true,
      image: 'cause_images/ddb58649769cae7989332e7ec2db41bc.jpeg',
      image_url:
        'https://storage.googleapis.com/ouno-app.appspot.com/cause_images/ddb58649769cae7989332e7ec2db41bc.jpeg',
      instagram: '',
      main_contact_email: '',
      main_contact_name: '',
      main_contact_phone: '',
      name: 'Dad Train',
      registered_charity: '',
      registration_number: '',
      tagline: '',
      tags: [{ created: '', name: 'Ocean', updated: '' }],
      updated: { seconds: 1629421284, nanoseconds: 145000000 },
      website: '',
    },
  ],
  locations: [],
  activeDraw: {
    ends_at: {
      _nanoseconds: 955000000,
      _seconds: 1631547782,
    },
    processed_at: null,
    amount: 0,
    cause_split: 0,
    id: 'qFkga3f0vbAmP6I6L06G',
  },
  pastDrawsList: {
    count: 0,
    list: [],
  },
  leaderBoardData: [],
  categories: [],
  countDown: [],
  user: {},
  resetEmailPassword: '',
  verifyEmail: '',
  error: {},
};

export function causes(state = initialState.causes, action) {
  switch (action.type) {
    case GET_CAUSES:
      return { causes: action.payload };
    case CREATE_CAUSE:
      state.causes?.push(action.payload);
      return { causes: state.causes };
    default:
      return state;
  }
}
export function tags(state = initialState.tags, action) {
  switch (action.type) {
    case GET_TAGS:
      return (state = action.payload);
    default:
      return state;
  }
}

export function categories(state = initialState.categories, action) {
  switch (action.type) {
    case GET_CATEGORIES:
      return { categories: action.payload };
    default:
      return state;
  }
}

export function user(state = initialState.user, action) {
  switch (action.type) {
    case LOGIN_USER:
      return { user: action.payload };
    default:
      return state;
  }
}

export function resetPassword(state = initialState.resetEmailPassword, action) {
  switch (action.type) {
    case SET_EMAIL_RESET_PASSWORD:
      if (action.payload) {
        return action.payload;
      }
      return state;
    case UNSET_EMAIL_RESET_PASSWORD:
      return '';
    default:
      return state;
  }
}
export function verifyEmail(state = initialState.verifyEmail, action) {
  switch (action.type) {
    case SET_VERIFY_EMAIL:
      if (action.payload) {
        return action.payload;
      }
      return state;
    case UNSET_VERIFY_EMAIL:
      return '';
    default:
      return state;
  }
}
export function activeDraw(state = initialState.activeDraw, action) {
  switch (action.type) {
    case GET_COUNTDOWN:
      return (state = action.payload.data);
    default:
      return state;
  }
}
export function pastDrawsList(state = initialState.pastDrawsList, action) {
  switch (action.type) {
    case GET_PASTDRAWLIST:
      return (state = action.payload.data);
    default:
      return state;
  }
}
export function leaderBoardData(state = initialState.leaderBoardData, action) {
  switch (action.type) {
    case GET_LEADERBOARDDATA:
      return (state = action.payload.data);
    default:
      return state;
  }
}
export function error(state = initialState.error, action) {
  const { error } = action;
  if (error) {
    return {
      error: error,
    };
  } else if (action.type === HIDE_ERROR) {
    return {
      error: null,
    };
  }

  return state;
}
