import React, { useState, useMemo, useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';
import search from '../../assets/images/Search.svg';
import filter from '../../assets/images/Filter.svg';
import { NavLink } from 'react-router-dom';
import weigher from '../../assets/images/play-section-weigher.svg';
import { SupportModal } from './SupportModal/SupportModal';
import './support-styles.scss';
import pen from '../../assets/images/support-pen.svg';
import clean from '../../assets/images/support-clean.svg';
import back from '../../assets/images/Back.svg';
import like from '../../assets/images/like.svg';
import deleteIcon from '../../assets/images/support-delete.svg';
import { useParams } from 'react-router';

export function Support() {
  const { name } = useParams();

  const causes = useSelector((state) => state.causes.causes);

  const recentlyCauses = causes.slice(causes.length - 5, causes.length);

  const selectCause = causes.filter((item) => item.name === name);

  const [splitPercentPeople, setSplitPeople] = useState(50);

  const splitPercentOuno = 100 - splitPercentPeople;
  const inputRange = useRef(0);
  const listAmount = useRef(0);
  const [selectedAmount, setSelectedAmount] = useState(0);
  const inputNumber = useRef();
  const changeSelectedPrice = (amount, event) => {
    if (event.classList.contains('support-contributing__amount__list__item')) {
      setSelectedAmount(amount);
      const arrayList = [...listAmount.current.children];
      arrayList.forEach((el) => el.classList.remove('active'));
      event.classList.add('active');
    }
    if (event.classList.contains('input_clear')) {
      setSelectedAmount(amount);
      const arrayList = [...listAmount.current.children];
      arrayList.forEach((el) => el.classList.remove('active'));
      inputNumber.current.value = 0;
    }
  };
  const inputSelectAmount = (value) => {
    setSelectedAmount(value);
  };
  const handleInputChange = () => {
    const min = inputRange.current.min;
    const max = inputRange.current.max;
    const value = inputRange.current.value;
    inputRange.current.style.backgroundSize =
      ((splitPercentPeople - min) * 100) / (max - min) + '% 100%';
    setSplitPeople(value);
  };
  const [openModal, setOpenModal] = useState(false);
  const changeModal = () => setOpenModal(true);
  const closeModal = () => setOpenModal(false);
  return (
    <section className="support">
      <SupportModal isOpen={openModal} closeModal={closeModal}></SupportModal>
      <section className={'support-causes'}>
        <h3 className={'support-causes__title'}>
          {name === undefined ? (
            'Support a Cause'
          ) : (
            <NavLink
              to={'/discover'}
              style={{ display: 'flex', alignItems: 'center', color: '#ffff' }}>
              {' '}
              <img src={back} alt="" style={{ marginRight: '20px' }} /> Support
              a Cause
            </NavLink>
          )}
        </h3>
        <section
          className="section-discover-causes__filtration"
          style={{ padding: '24px 0' }}>
          <div className="section-discover-causes__filtration__input">
            <NavLink
              to={'/discover'}
              style={{ width: '100%', display: 'flex', alignItems: 'center' }}>
              <img src={search} alt="" />
              <input type="text" placeholder="Search" />
            </NavLink>
          </div>
          <NavLink to={'/discover'}>
            <span
              className="section-discover-causes__filtration__sort"
              style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
              }}>
              <img src={filter} alt="" />
            </span>
          </NavLink>
        </section>
        <h3 className={'support__subtitle'}>I`m supporting:</h3>
        <ul className={'support-causes__list'}>
          {name === undefined
            ? recentlyCauses.map((item) => {
                return (
                  <li className={'support-causes__list__item'} key={item.name}>
                    <NavLink to={`/supportSelectCause/${item.name}`}>
                      <div
                        className="support-causes__list__item__content"
                        style={{ backgroundImage: `url(${item.image_url})` }}>
                        {item.name}
                      </div>
                    </NavLink>
                  </li>
                );
              })
            : selectCause.map((item) => {
                return (
                  <li
                    className={'support-causes__list__item selected'}
                    key={item.name}>
                    <div
                      className="support-causes__list__item__content"
                      style={{
                        backgroundImage: `url(${item.image_url})`,
                        position: 'relative',
                      }}>
                      {item.name}
                      <img
                        style={{
                          position: 'absolute',
                          right: '5px',
                          bottom: '5px',
                          cursor: 'pointer',
                        }}
                        src={like}
                        alt=""
                      />
                      <img
                        style={{
                          position: 'absolute',
                          right: '5px',
                          top: '5px',
                          cursor: 'pointer',
                        }}
                        src={deleteIcon}
                        alt=""
                      />
                    </div>
                  </li>
                );
              })}
        </ul>
      </section>
      <section className={'support-contributing'}>
        <h3 className={'support__subtitle'}>I'm contributing:</h3>
        <div className={'support-contributing__amount'}>
          <ul ref={listAmount} className={'support-contributing__amount__list'}>
            <li
              className={'support-contributing__amount__list__item input '}
              onClick={(event) => changeSelectedPrice(10, event.target)}>
              <div>
                <img src={pen} alt="" />
                <input
                  ref={inputNumber}
                  defaultValue={0}
                  onChange={(event) => inputSelectAmount(event.target.value)}
                  type="number"
                />
                <span>
                  <img
                    className={'input_clear'}
                    src={clean}
                    alt=""
                    onClick={(event) => changeSelectedPrice(0, event.target)}
                  />
                </span>
              </div>
            </li>
            <li
              className={'support-contributing__amount__list__item fix'}
              onClick={(event) => changeSelectedPrice(10, event.target)}>
              <div>$10</div>
            </li>
            <li
              className={'support-contributing__amount__list__item fix'}
              onClick={(event) => changeSelectedPrice(50, event.target)}>
              <div>$50</div>
            </li>
            <li
              className={'support-contributing__amount__list__item fix'}
              onClick={(event) => changeSelectedPrice(150, event.target)}>
              <div>$150</div>
            </li>
          </ul>
        </div>
        <div className={'support-split'}>
          <div
            className={''}
            style={{ display: 'flex', flexDirection: 'column' }}>
            <div
              className={'support-split__modal'}
              onClick={() => changeModal()}>
              i
            </div>
            <div className={'split__price'}>
              <div className={'split__price__bar'}>
                <img src={weigher} alt="" />
                <div>
                  <h3 className={'split__price__bar__title'}>The split:</h3>
                  <h3 className={'split__price__bar__percent'}>
                    {splitPercentOuno}%
                  </h3>
                  <h3 className={'split__price__bar__choice'}>
                    Ouno`s <br /> Choice
                  </h3>
                </div>
              </div>
              <div className={'split__price__center-bar'}>
                <div className={'passive-bar'}>
                  <div
                    className={'active-bar_ouno'}
                    style={{ width: `${splitPercentOuno}%` }}></div>
                  <div
                    className={'active-bar_people'}
                    style={{ width: `${splitPercentPeople}%` }}></div>
                </div>
              </div>
              <div style={{ alignSelf: 'end' }}>
                <h3 className={'split__price__bar__percent'}>
                  {splitPercentPeople}%
                </h3>
                <h3 className={'split__price__bar__choice'}>
                  People's <br /> Choice
                </h3>
              </div>
            </div>
            <div>
              <input
                type="range"
                className={'split__price__bar__input'}
                ref={inputRange}
                min={0}
                max={100}
                style={{ backgroundSize: `${splitPercentPeople}% 100%` }}
                onChange={() => handleInputChange()}
              />
            </div>
          </div>
        </div>
        <button className={'support-btn'}>support campaign</button>
      </section>
    </section>
  );
}
