import { combineReducers } from 'redux';
import {
  causes,
  tags,
  activeDraw,
  pastDrawsList,
  leaderBoardData,
  categories,
  user,
  resetPassword as resetEmailPassword,
  verifyEmail,
  error,
} from './connect';

export const rootReducer = combineReducers({
  causes,
  tags,
  activeDraw,
  pastDrawsList,
  leaderBoardData,
  categories,
  user,
  resetEmailPassword,
  verifyEmail,
  error,
});
