import React, { PureComponent, useEffect, useMemo } from 'react';
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from 'recharts';
import back from '../../assets/images/Back.svg';
import backgroundProgress from '../../assets/images/background-funds-raised.svg';
import present from '../../assets/images/funds-raised-present.svg';
import helpHand from '../../assets/images/funds-raised-hand.svg';
import helpMan from '../../assets/images/funds-raised-man.svg';
import { NavLink } from 'react-router-dom';
import './fundsRaised.scss';
import { useSelector } from 'react-redux';

export function FundsRaised() {
  const months = [
    'Jan',
    'Feb',
    'March',
    'April',
    'May',
    'June',
    'July',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dec',
  ];
  const activeDraw = useSelector((state) => state.activeDraw);
  const pastDrawsList = useSelector((state) => state.pastDrawsList);
  const graphData = useMemo(() => {
    return pastDrawsList.list
      .slice(pastDrawsList.list.length - 6, pastDrawsList.list.length)
      .map((el) => {
        return {
          ...el,
          days: `${new Date(el.created._nanoseconds).getDay()} ${
            months[new Date(el.created._nanoseconds).getMonth()]
          }`,
        };
      });
  }, [pastDrawsList]);
  return (
    <section className={'funds-raised'}>
      <section className={'funds-raised__title'}>
        <NavLink to={'/'} className={'funds-raised__title__back'}>
          <img src={back} />
          Funds Raised
        </NavLink>
        <button className={'funds-raised__title__payment'}>
          Help us break a new record
        </button>
      </section>
      <div
        className={'funds-raised__weeks-progress'}
        style={{ backgroundImage: `url(${backgroundProgress})` }}>
        <img src={present} alt="" />
        this week's progress ${activeDraw.amount}
      </div>
      <div className={'funds-raised__post'}>
        <h4 className={'funds-raised__post__button_left'}>Post funds raised</h4>
        <h4 className={'funds-raised__post__button'}>73% raised</h4>
      </div>
      <ResponsiveContainer
        width="63%"
        height="36%"
        className={'funds-raised__diagram'}>
        <AreaChart width={500} height={400} data={graphData}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey={'days'} />
          <YAxis />
          <Tooltip />
          <Area
            Area
            type="monotone"
            dataKey={'amount'}
            stroke="#D680F1"
            fill="#D680F1"
          />
        </AreaChart>
      </ResponsiveContainer>
      <div className={'funds-raised__current-help'}>
        <img src={helpHand} alt="" />
        <h5>
          To date OUNO has raised $10,000,000 for positive causes worldwide!
        </h5>
      </div>
      <div className={'funds-raised__current-help'}>
        <img src={helpMan} alt="" />
        <h5>
          To date OUNO has given $10,000,000 back to the people who support the
          movement!
        </h5>
      </div>
    </section>
  );
}
