import React, { useEffect, useState } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import {
  getCauses,
  getActiveDraw,
  getTags,
  getPastDrawsList,
  getLeaderBoardData,
  getCurrentUser,
} from './redux/actions';
import { Footer } from './components/Footer/footer';
import { Navbar } from './components/Navbar/navbar';
import { Header } from './components/Header/header';
import { OurMission } from './pages/OurMission';
import { Support } from './pages/Support/support';
import { Registration } from './pages/Registration/Registration';
import { Discover } from './pages/socialImpact/discover';
import { SelectCause } from './pages/socialImpact/selectCause/selectCause';
import { Suggest } from './pages/Suggest/Suggest';
import { ErrorModal } from './components/errorModal/errorModal';
import { MainPageHelpCenter } from './pages/helpCenter/mainPageHelpCenter/mainPageHelpCenter';
import { FaqPageHelpCenter } from './pages/helpCenter/faqPageHelpCenter/faqPageHelpCenter';
import { FeedbackPageHelpCenter } from './pages/helpCenter/feedbackPageHelpCenter/feedbackPageHelpCenter';
import { FundsRaised } from './pages/fundsRaised/fundsRaised';
import { PeoplesWinner } from './pages/peoplesWinner/peoplesWinner';
import { LeaderBord } from './pages/LeaderBoard/LeaderBord';
import { fetchWithTimeout } from './features/fetchWithTimeout';
import { Wallet } from './pages/Wallet/Wallet';
import { Transfer } from './pages/Wallet/Transfer/Transfer';
function App() {
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  useEffect(() => {
    async function checkConnection() {
      try {
        const response = await fetchWithTimeout(
          'https://api.publicapis.org/random'
        );
        console.log(response);
      } catch (err) {
        setOpen(true);
      }
    }
    checkConnection();
  }, []);

  const handleClose = () => setOpen(false);

  useEffect(() => {
    dispatch(getCauses());
    dispatch(getTags());
    dispatch(getActiveDraw());
    dispatch(getPastDrawsList());
    dispatch(getLeaderBoardData());
  }, []);
  return (
    <div className="App">
      <BrowserRouter>
        <div className="container">
          <Header />
          <main>
            <div className="wrapper">
              <div className="main_content">
                <Navbar />
                <Switch>
                  <Route path={'/'} exact component={OurMission} />
                  <Route path={'/support'} exact component={Support} />
                  <Route path={'/discover'} component={Discover} />
                  <Route
                    path={'/register/register-form'}
                    component={Registration}
                  />
                  <Route path={'/selectCause/:name'} component={SelectCause} />
                  <Route path={'/suggest'} component={Suggest} />
                  <Route
                    path={'/mainPageHelpCenter'}
                    component={MainPageHelpCenter}
                  />
                  <Route
                    path={'/faqPageHelpCenter'}
                    component={FaqPageHelpCenter}
                  />
                  <Route
                    path={'/feedbackPageHelpCenter'}
                    component={FeedbackPageHelpCenter}
                  />
                  <Route path={'/fundsRaised'} component={FundsRaised} />
                  <Route path={'/peoplesWinner'} component={PeoplesWinner} />
                  <Route path={'/leaderBoard'} component={LeaderBord} />
                  <Route
                    path={'/supportSelectCause/:name'}
                    component={Support}
                  />
                  <Route path={'/wallet'} component={Wallet} />
                  <Route path={'/transfer'} component={Transfer} />
                </Switch>
              </div>
            </div>
          </main>
          <Footer />
        </div>
        <ErrorModal isOpen={open} onClose={handleClose} />
      </BrowserRouter>
    </div>
  );
}

export default App;
