import { NavLink } from 'react-router-dom';
import arrowDownIcon from '../../../assets/images/arrow-down-icon.svg';
import walletIcon from '../../../assets/images/wallet-icon.png';
import settingsIcon from '../../../assets/images/settings-icon.png';
import { useEffect, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import arrowUpIcon from '../../../assets/images/arrow-up-icon.svg';

export function UserMenu(props) {
  const { onLoginClick } = props;
  const [showSubmenu, setShowSubmenu] = useState(false);
  const user = useSelector((state) => state.user.user);
  const ref = useRef();
  useEffect(() => {
    function handleClickOutside(event) {
      if (ref.current && !ref.current.contains(event.target)) {
        setShowSubmenu(false);
      }
    }
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [ref]);
  return (
    <div className="header-profile-menu" ref={ref}>
      <div
        className={`header-profile-container ${
          user && 'header-profile-container--authorized'
        }`}
        onClick={user ? () => setShowSubmenu(!showSubmenu) : onLoginClick}>
        <div
          className={`header-profile-image ${
            user && 'header-profile-image--authorized'
          }`}></div>
        <div>
          <p>{user ? user.displayName : 'Log In or Sign Up'}</p>
          {user && <p>{user.email}</p>}
        </div>
        {user && (
          <img
            src={showSubmenu ? arrowUpIcon : arrowDownIcon}
            alt="arrow down icon"
            className="header-profile-arrow"
          />
        )}
      </div>
      {user && (
        <div
          className={`header-profile-submenu ${
            showSubmenu && 'header-profile-submenu--display'
          }`}>
          <NavLink
            className="header-profile-flex-row header-profile-link"
            to="/wallet"
            onClick={() => setShowSubmenu(!showSubmenu)}>
            <img
              src={walletIcon}
              alt="wallet icon"
              className="header-profile-icon"
            />
            <div>
              <h3>Wallet</h3>
              <p>Transaction history, transfer money out</p>
            </div>
          </NavLink>
          <NavLink
            className="header-profile-flex-row header-profile-link"
            to="/"
            onClick={() => setShowSubmenu(!showSubmenu)}>
            <img
              src={settingsIcon}
              alt="settings icon"
              className="header-profile-icon"
            />
            <div>
              <h3>Settings</h3>
              <p>Manage your profile</p>
            </div>
          </NavLink>
        </div>
      )}
    </div>
  );
}
