/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useEffect, useState } from 'react';
import ReactModal from 'react-modal';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import facebookIcon from '../../assets/images/facebook-login-icon.svg';
import googleIcon from '../../assets/images/google-icon.svg';
import appleLogo from '../../assets/images/apple-icon.png';
import passwordIcon from '../../assets/images/passwordIcon.svg';
import { emailRegExp } from '../../features/regexps';
import { loginUser, loginWithGoogle, resetError } from '../../redux/actions';
import { ResetPasswordModal } from './resetPasswordModal';
import { SignUpModel } from './signUpModal';
import './authorization-modal.scss';
import { EMAIL_NOT_VERIFIED } from '../../redux/errors';

const LOCAL_ERRORS = ['auth/user-not-found', 'auth/wrong-password'];

export function LoginModal({ setLoginModalOpen, isLoginModalOpen }) {
  const onClose = () => setLoginModalOpen(false);
  const isOpen = isLoginModalOpen;
  const [isSignUpModelOpen, setSignUpModelOpen] = useState(false);
  const [isResetPasswordModelOpen, setResetPasswordModelOpen] = useState(false);
  const onSignUpModelOpen = () => setSignUpModelOpen(true);
  const onResetPasswordModelOpen = () => setResetPasswordModelOpen(true);
  const onLoginModalOpen = () => setLoginModalOpen(true);
  const dispatch = useDispatch();
  const [isEmailIncorrect, setEmailIncorrect] = useState(false);
  const [wasTriedSubmitted, setWasTriedSubmitted] = useState(false);
  const [isShowPassword, setShowPassword] = useState(false);

  const onSubmit = (data) => {
    setWasTriedSubmitted(true);
    console.log(data);
    dispatch(loginUser(data.email, data.password));
  };
  const onErrorSubmit = (errors) => {
    setWasTriedSubmitted(true);
    setEmailIncorrect(true);
  };
  const error = useSelector((state) => state.error.error);
  const { register, handleSubmit, reset } = useForm();
  const emailRegister = register('email', {
      pattern: emailRegExp,
      required: true,
    }),
    passwordRegister = register('password', { required: true }),
    rememberRegister = register('rememberMe');

  const onModalClose = () => {
    onClose();
    reset();
    setWasTriedSubmitted(false);
    setEmailIncorrect(false);
    setShowPassword(false);
    if (LOCAL_ERRORS.includes(error?.code)) dispatch(resetError());
  };
  const user = useSelector((state) => state.user.user);
  const state = useSelector((state) => state);
  console.log(state);
  useEffect(() => {
    if (user || error?.code === EMAIL_NOT_VERIFIED) {
      onModalClose();
    } else if (LOCAL_ERRORS.includes(error?.code)) {
      setWasTriedSubmitted(true);
      setEmailIncorrect(true);
    } else {
      setWasTriedSubmitted(false);
      setEmailIncorrect(false);
    }
  }, [reset, user, error]);
  const onGoogleLogin = () => dispatch(loginWithGoogle());
  const customStyles = {
    overlay: {
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
      position: 'fixed',
      height: '100%',
      width: '100%',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'flex-start',
      overflowY: 'scroll',
    },
    content: {
      minWidth: '655px',
      padding: '58px 70px',
      backgroundColor: '#272A33',
      borderRadius: '23px',
      textAlign: 'center',
      outline: 'none',
      position: 'relative',
      zIndex: 0,
      margin: 'auto 0',
    },
  };
  const showPassword = () => setShowPassword(!isShowPassword);
  const goToForgotPasswordModal = () => {
    onResetPasswordModelOpen();
    onModalClose();
  };
  const emailLabelErrorStyle = {
    visibility: isEmailIncorrect ? 'visible' : 'hidden',
  };
  const passwordType = isShowPassword ? 'text' : 'password';
  const onSignUpModelClose = () => setSignUpModelOpen(false);
  const onResetPasswordModelClose = () => setResetPasswordModelOpen(false);
  return (
    <>
      <SignUpModel
        onClose={onSignUpModelClose}
        isOpen={isSignUpModelOpen}
        onLoginModalOpen={onLoginModalOpen}
      />
      <ResetPasswordModal
        onClose={onResetPasswordModelClose}
        onOpen={onResetPasswordModelOpen}
        isOpen={isResetPasswordModelOpen}
        onLoginModalOpen={onLoginModalOpen}
      />
      <ReactModal
        isOpen={isOpen}
        onRequestClose={onModalClose}
        style={customStyles}
        className="authorization-modal">
        {
          <article className="authorization-modal-content">
            <div className="authorization-modal__right">
              <p className="authorization-modal__skip" onClick={onModalClose}>
                Skip for now
              </p>
            </div>
            <div style={{ textAlign: 'left' }}>
              <h2 className="authorization-modal__title">Getting Started</h2>
            </div>
            <p className="authorization-modal__info">
              Log In or Sign Up to make the world a better place!
            </p>
            <section className="authorization-modal__flex-row">
              <button
                className="authorization-modal__short-btn"
                onClick={onGoogleLogin}>
                <img
                  src={googleIcon}
                  alt="google icon"
                  className="authorization-modal__icon"
                />
              </button>
              <button className="authorization-modal__short-btn authorization-modal__short-btn--dark-blue">
                <img
                  src={facebookIcon}
                  alt="facebook icon"
                  className="authorization-modal__icon"
                />
              </button>
            </section>
            <section className="authorization-modal__flex-row">
              <button className="authorization-modal__short-btn">
                <img
                  src={appleLogo}
                  alt="microsoft icon"
                  className="authorization-modal__icon"
                />
              </button>
            </section>
            <div className="authorization-modal__separator">or</div>
            <form
              className="authorization-modal__form"
              onSubmit={handleSubmit(onSubmit, onErrorSubmit)}>
              <div className="authorization-modal__text-input">
                <label className="authorization-modal__label" htmlFor="email">
                  Email Address
                </label>
                <label
                  className="authorization-modal__label authorization-modal__label--error"
                  htmlFor="email"
                  style={emailLabelErrorStyle}>
                  *Error: invalid email
                </label>
                <div
                  className={`authorization-modal__input-wrapper ${
                    isEmailIncorrect &&
                    'authorization-modal__input-wrapper--with-error'
                  }`}>
                  <input
                    className={`authorization-modal__input ${
                      isEmailIncorrect && 'authorization-modal__input--invalid'
                    }`}
                    type="text"
                    name="email"
                    id="email"
                    placeholder="Enter your email"
                    {...emailRegister}
                    onChange={(event) => {
                      emailRegister.onChange(event);
                      setEmailIncorrect(
                        !emailRegExp.test(event.target.value) &&
                          wasTriedSubmitted
                      );
                    }}
                  />
                </div>
              </div>
              <div className="authorization-modal__text-input">
                <label
                  className="authorization-modal__label"
                  htmlFor="password">
                  Password
                </label>
                <div className="authorization-modal__password-wrapper authorization-modal__input-wrapper">
                  <input
                    className="authorization-modal__input"
                    type={passwordType}
                    name="password"
                    id="password"
                    placeholder="Enter your password"
                    {...passwordRegister}
                  />
                  <img
                    src={passwordIcon}
                    alt="eye icon"
                    onClick={showPassword}
                  />
                </div>
              </div>
              <div className="authorization-modal__flex-row authorization-modal__flex-row--margin">
                <label className="authorization-modal__checkbox authorization-modal__label">
                  Remember me
                  <input type="checkbox" {...rememberRegister} />
                  <span className="authorization-modal__checkmark"></span>
                </label>
                <a
                  className="authorization-modal__link"
                  onClick={goToForgotPasswordModal}>
                  Forgot password?
                </a>
              </div>
              <button
                type="submit"
                className="authorization-modal__btn authorization-modal__btn--gradient">
                Log In
              </button>
            </form>
            <button
              className="authorization-modal__btn authorization-modal__btn--dark"
              onClick={() => {
                onSignUpModelOpen();
                onModalClose();
              }}>
              Sign up
            </button>
            <footer className="authorization-modal__note">
              By selecting any of the above you agree to Ouno's ToU and PP
            </footer>
          </article>
        }
      </ReactModal>
    </>
  );
}
