import React from 'react';
import './header-styles.scss';
import { NavLink } from 'react-router-dom';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { UserMenu } from './userMenu/userMenu';
import { LoginModal } from '../authorizationModals/loginModal';

export function Header() {
  const [showLoginModal, setShowLoginModal] = useState(false);
  const activeDraw = useSelector((state) => state.activeDraw);
  const [isLoginModalOpen, setLoginModalOpen] = useState(false);
  const onLoginClick = (event) => setLoginModalOpen(true);
  const amount = useSelector((state) => state.getActiveDraw);
  return (
    <header className="header">
      <LoginModal
        isLoginModalOpen={isLoginModalOpen}
        setLoginModalOpen={setLoginModalOpen}
      />
      <div className="wrapper">
        <div className="header-content">
          <NavLink to={'/'} exact>
            <div className="header-logo"></div>
          </NavLink>
          <div className="header-content-values">
            <div className="header-values">
              <div className="header-values-raised">
                <NavLink to={'/fundsRaised'}>
                  <span className="label">Raised</span>
                  <span className="value">$ {activeDraw.amount}</span>
                </NavLink>
              </div>
              <div className="header-values-countdown">
                <span className="label">Countdown</span>
                <span className="value">6 : 23 : 48 : 26</span>
              </div>
            </div>
            <div className="header-profile">
              <UserMenu onLoginClick={onLoginClick} />
              <div className="header-profile-coins">
                <div className="header-profile-coins-icon"></div>
                <span className="header-profile-coins-value">0</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
}
