import React, { useState } from 'react';
import ReactModal from 'react-modal';
import '../support-styles.scss';
import close from '../../../assets/images/close-icon.svg';

export function SupportModal({ isOpen, closeModal }) {
  const customStyles = {
    overlay: {
      zIndex: 15,
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
    },
    content: {
      width: '582px',
      padding: '32px',
      backgroundColor: '#272A33',
      borderRadius: '23px',
      border: 'none',
      textAlign: 'center',
      outline: 'none',
      position: 'relative',
      zIndex: 0,
      margin: 'auto',
    },
  };

  return (
    <ReactModal isOpen={isOpen} style={customStyles} ariaHideApp={false}>
      <div className={'support-modal'}>
        <div className={'support-modal__close'} onClick={() => closeModal()}>
          <img src={close} alt="" />
        </div>
        <h3 className={'support-modal__title'}>The Split explain</h3>
        <p className={'support-modal__description'}>
          'The Split' is how you would like to share your contribution between
          your choice (Peoples Choice) and Ounos Choice.
        </p>
        <button className={'support-modal__btn'} onClick={() => closeModal()}>
          Done
        </button>
      </div>
    </ReactModal>
  );
}
