import { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import passwordIcon from '../../assets/images/passwordIcon.svg';
import ReactModal from 'react-modal';
import { emailRegExp } from '../../features/regexps';
import { VerifyEmailModal } from './verifyEmailModal';
import { useDispatch, useSelector } from 'react-redux';
import { resetError, signUp } from '../../redux/actions';
import { EMAIL_NOT_VERIFIED } from '../../redux/errors';

const LOCAL_ERRORS = ['auth/email-already-in-use'];

export function SignUpModel({ isOpen, onClose, onLoginModalOpen }) {
  // eslint-disable-next-line no-unused-vars
  const { register, handleSubmit, reset } = useForm();
  const dispatch = useDispatch();
  const [isVerifyEmailModalOpen, setVerifyEmailModalOpen] = useState(false);
  const onVerifyEmailModalOpen = () => setVerifyEmailModalOpen(true);
  const onVerifyEmailModalClose = () => setVerifyEmailModalOpen(false);
  const onModalClose = () => {
    onClose();
    reset();
    if (LOCAL_ERRORS.includes(error?.code)) dispatch(resetError());
  };
  const error = useSelector((state) => state.error.error);
  const email = useSelector((state) => state.verifyEmail);
  const [isEmailIncorrect, setEmailIncorrect] = useState(false);
  const [wasTriedSubmitted, setWasTriedSubmitted] = useState(false);
  const [isShowPassword, setShowPassword] = useState(false);
  const setStates = {
    email: setEmailIncorrect,
  };
  const onErrorSubmit = (errors) => {
    setWasTriedSubmitted(true);
    for (const [key] of Object.entries(errors)) {
      setStates[key] && setStates[key](true);
    }
  };
  useEffect(() => {
    if (error?.code === EMAIL_NOT_VERIFIED) {
      onModalClose();
      onVerifyEmailModalOpen();
    } else if (email && !error) {
      onVerifyEmailModalOpen();
      onModalClose();
    } else if (LOCAL_ERRORS.includes(error?.code)) {
      setEmailIncorrect(true);
      setWasTriedSubmitted(true);
    } else {
      setEmailIncorrect(false);
      setWasTriedSubmitted(false);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [error, email]);
  const emailRegister = register('email', {
      pattern: emailRegExp,
      required: true,
    }),
    passwordRegister = register('password'),
    nameRegister = register('name'),
    countryRegister = register('country');

  const customStyles = {
    overlay: {
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
      position: 'fixed',
      height: '100%',
      width: '100%',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'flex-start',
      overflowY: 'scroll',
    },
    content: {
      minWidth: '655px',
      padding: '58px 70px',
      backgroundColor: '#272A33',
      borderRadius: '23px',
      textAlign: 'center',
      outline: 'none',
      position: 'relative',
      zIndex: 0,
      margin: 'auto 0',
    },
  };
  const backToLoginModal = () => {
    onModalClose();
    onLoginModalOpen();
  };
  const onSubmit = (data) => {
    console.log(data);
    const { email, password, country, name } = data;
    dispatch(signUp({ email, password, country, fullName: name }));
  };
  const showPassword = () => setShowPassword(!isShowPassword);
  const onEmailChange = (event) => {
    emailRegister.onChange(event);
    setEmailIncorrect(
      !emailRegExp.test(event.target.value) && wasTriedSubmitted
    );
  };
  const passwordType = isShowPassword ? 'text' : 'password';
  return (
    <>
      <VerifyEmailModal
        onClose={onVerifyEmailModalClose}
        isOpen={isVerifyEmailModalOpen}
        email={email}
        onLoginModalOpen={onLoginModalOpen}
      />
      <ReactModal
        isOpen={isOpen}
        onRequestClose={onModalClose}
        style={customStyles}
        className="authorization-modal">
        {
          <article className="authorization-modal-content">
            <div className="authorization-modal__right">
              <p
                className="authorization-modal__back"
                onClick={backToLoginModal}>
                Back
              </p>
            </div>
            <div className="authorization-modal__left">
              <h2 className="authorization-modal__title">Sign Up</h2>
            </div>
            <p className="authorization-modal__info">
              Seems you're ready to join the mission to change the world....
            </p>
            <form
              className="authorization-modal__form"
              onSubmit={handleSubmit(onSubmit, onErrorSubmit)}>
              <div className="authorization-modal__text-input">
                <label
                  className="authorization-modal__label authorization-modal__label--required"
                  htmlFor="name">
                  Full Name
                </label>
                <div className="authorization-modal__input-wrapper">
                  <input
                    className="authorization-modal__input"
                    type="text"
                    name="name"
                    id="name"
                    placeholder="Enter your full name"
                    {...nameRegister}
                  />
                </div>
              </div>
              <div className="authorization-modal__text-input">
                <label className="authorization-modal__label" htmlFor="country">
                  Country
                </label>
                <div className="authorization-modal__input-wrapper">
                  <input
                    className="authorization-modal__input"
                    type="text"
                    name="country"
                    id="country"
                    placeholder="Enter your country"
                    {...countryRegister}
                  />
                </div>
              </div>
              <div className="authorization-modal__text-input">
                <label
                  className="authorization-modal__label authorization-modal__label--required"
                  htmlFor="email">
                  Email Address
                </label>
                <div
                  className={`authorization-modal__input-wrapper ${
                    isEmailIncorrect &&
                    'authorization-modal__input-wrapper--with-error'
                  }`}>
                  <input
                    className={`authorization-modal__input ${
                      isEmailIncorrect && 'authorization-modal__input--invalid'
                    }`}
                    type="text"
                    name="email"
                    id="email"
                    placeholder="Enter your email"
                    {...emailRegister}
                    onChange={onEmailChange}
                  />
                </div>
                {isEmailIncorrect && (
                  <label
                    className="authorization-modal__label authorization-modal__label--error"
                    htmlFor="email">
                    Sorry, this email is invalid. Let's try another one.
                  </label>
                )}
              </div>
              <div className="authorization-modal__text-input">
                <label
                  className="authorization-modal__label authorization-modal__label--required"
                  htmlFor="password">
                  Create Password
                </label>
                <div className="authorization-modal__password-wrapper authorization-modal__input-wrapper">
                  <input
                    className="authorization-modal__input"
                    type={passwordType}
                    name="password"
                    id="password"
                    placeholder="Enter your password"
                    {...passwordRegister}
                  />
                  <img
                    src={passwordIcon}
                    alt="eye icon"
                    onClick={showPassword}
                  />
                </div>
              </div>
              <button
                type="submit"
                className="authorization-modal__btn authorization-modal__btn--gradient authorization-modal__btn--with-shadow"
                style={{ marginTop: '22px' }}>
                Sign Up
              </button>
            </form>
          </article>
        }
      </ReactModal>
    </>
  );
}
