import React, { useMemo } from 'react';
import ouno from '../../assets/images/leaderBoard-ouno.png';
import './LeaderBord.scss';
import { NavLink } from 'react-router-dom';
import back from '../../assets/images/Back.svg';
import peopleChoice from '../../assets/images/split_people_choice.png';
import ounoChoice from '../../assets/images/split_ouno_choice.png';
import { useSelector } from 'react-redux';
export function LeaderBord() {
  const activeDraw = useSelector((state) => state.activeDraw);

  return (
    <section className={'split'}>
      <section className={'split__title'}>
        <NavLink to={'/'} className={'split__title__back'}>
          <img src={back} />
          the split
        </NavLink>
      </section>
      <section style={{ padding: '31px' }}>
        <section>
          <div className={'pie-diagram'}>
            <div className={'pie-diagram__ring'}>
              <svg
                className={'pie-diagram__ring__svg'}
                width="275px"
                height="257px">
                <linearGradient id="g2">
                  <stop stopColor="#4DA1FF" />
                  <stop offset="1" stopColor="#4DFFDF" />
                </linearGradient>
                <circle
                  cx="50%"
                  cy="50%"
                  r="120"
                  className={'pie-diagram__ring__svg__circle_passive'}
                />
                <circle
                  cx="50%"
                  cy="50%"
                  r="120"
                  className={'pie-diagram__ring__svg__circle_active'}
                  style={{
                    strokeDasharray: `${120 * 6}`,
                    strokeDashoffset: `${
                      120 * 6 - (120 * 6 * activeDraw.cause_split) / 100
                    }`,
                    stroke: 'url(#g2)',
                    strokeLinecap: 'round',
                  }}
                />
                <linearGradient id="g1">
                  <stop stopColor="#765BEB" />
                  <stop offset="1" stopColor="#D680F1" />
                </linearGradient>
                <circle
                  cx="50%"
                  cy="50%"
                  r="95"
                  className={'pie-diagram__ring__svg__circle_passive'}
                />
                <circle
                  cx="50%"
                  cy="50%"
                  r="95"
                  className={'pie-diagram__ring__svg__circle_active'}
                  style={{
                    strokeDasharray: `${95 * 6}`,
                    strokeDashoffset: `${
                      95 * 6 - (95 * 6 * (100 - activeDraw.cause_split)) / 100
                    }`,
                    stroke: 'url(#g1)',
                    strokeLinecap: 'round',
                  }}
                />
                <image
                  href={ouno}
                  x="36.5%"
                  y="15%"
                  transform="rotate(90 140 105)"
                />
              </svg>
            </div>
            <div className={'pie-diagram__data'}>
              <h3 className={'pie-diagram__data__title'}>total progress</h3>
              <h3 className={'pie-diagram__data__money'}>$50,000,000</h3>
              <h3 className={'pie-diagram__data__last'}>
                Compared to $21,504 last week
              </h3>
              <div className={'pie-diagram__data__choice'}>
                <div style={{ paddingRight: '20px' }}>
                  <h4 className={'pie-diagram__data__choice__percent'}>
                    {activeDraw.cause_split}%
                  </h4>
                  <h4 className={'pie-diagram__data__choice__people'}>
                    people's choice
                  </h4>
                </div>
                <div className={'pie-diagram__data__graphs__graph'}>
                  <h4 className={'pie-diagram__data__choice__percent'}>
                    {100 - activeDraw.cause_split}%
                  </h4>
                  <h4 className={'pie-diagram__data__choice__ouno'}>
                    ouno's choice
                  </h4>
                </div>
              </div>
            </div>
          </div>
        </section>
        <section className={'split__choice'}>
          <div style={{ padding: '0 15px', width: '50%' }}>
            <div className={'split__choice__tile'}>
              <div className={'split__choice__tile__icon'}>
                <img src={peopleChoice} alt="" />
              </div>
              <div className={'split__choice__tile__content'}>
                <h3 className={'split__choice__tile__content__title'}>
                  people's choice:
                </h3>
                <h3 className={'split__choice__tile__content__money'}>
                  $ 35,000,000
                </h3>
              </div>
            </div>
          </div>
          <div style={{ padding: '0 15px', width: '50%' }}>
            <div className={'split__choice__tile'}>
              <div className={'split__choice__tile__icon'}>
                <img src={ounoChoice} alt="" />
              </div>
              <div className={'split__choice__tile__content'}>
                <h3 className={'split__choice__tile__content__title'}>
                  ouno choice:
                </h3>
                <h3 className={'split__choice__tile__content__money'}>
                  $ 15,000,000
                </h3>
              </div>
            </div>
          </div>
        </section>
        <h3 style={{ color: '#ffff', paddingBottom: '15px' }}>leaderboard:</h3>
        <table style={{ width: '100%' }}>
          <tbody>
            <tr
              style={{
                borderBottom: '2px solid #414348',
                borderTop: '2px solid #414348',
              }}>
              <td style={{ color: '#ffff', padding: '15px 0' }}>29 Oct 20</td>
              <td style={{ color: '#9292C1', textAlign: 'end' }}>$2,789,322</td>
            </tr>
            <tr
              style={{
                borderBottom: '2px solid #414348',
                borderTop: '2px solid #414348',
              }}>
              <td style={{ color: '#ffff', padding: '15px 0' }}>29 Oct 20</td>
              <td style={{ color: '#9292C1', textAlign: 'end' }}>$2,789,322</td>
            </tr>
            <tr
              style={{
                borderBottom: '2px solid #414348',
                borderTop: '2px solid #414348',
              }}>
              <td style={{ color: '#ffff', padding: '15px 0' }}>29 Oct 20</td>
              <td style={{ color: '#9292C1', textAlign: 'end' }}>$2,789,322</td>
            </tr>
            <tr
              style={{
                borderBottom: '2px solid #414348',
                borderTop: '2px solid #414348',
              }}>
              <td style={{ color: '#ffff', padding: '15px 0' }}>29 Oct 20</td>
              <td style={{ color: '#9292C1', textAlign: 'end' }}>$2,789,322</td>
            </tr>
            <tr
              style={{
                borderBottom: '2px solid #414348',
                borderTop: '2px solid #414348',
              }}>
              <td style={{ color: '#ffff', padding: '15px 0' }}>29 Oct 20</td>
              <td style={{ color: '#9292C1', textAlign: 'end' }}>$2,789,322</td>
            </tr>
          </tbody>
        </table>
      </section>
    </section>
  );
}
