import React, { useState, useMemo } from 'react';
import { useSelector } from 'react-redux';
import './discover.scss';
import search from '../../assets/images/Search.svg';
import filter from '../../assets/images/Filter.svg';
import like from '../../assets/images/like.svg';
import empty_element_sort from '../../assets/images/empty_element_sort.svg';
import { NavLink } from 'react-router-dom';
import back from '../../assets/images/Back.svg';
import { SortModal } from '../../components/modal/sortModal';

function filtration(causes, inputsValue, selectedCategories) {
  if (selectedCategories.length !== 0) {
    return [
      causes.filter((element) =>
        element.tags.some((currentItem) =>
          selectedCategories.some((item) => item === currentItem.name)
        )
      ),
    ];
  } else {
    const liveSearch = causes.filter((item) =>
      item.name.toLocaleLowerCase().includes(inputsValue)
    );
    const liveSearchTags = liveSearch
      .flatMap((item) => [...item.tags])
      .map(({ name }) => name);
    const uniqueTags = [...new Set(liveSearchTags)];
    const suggested = causes.filter((item) =>
      item.tags.some((currentItem) =>
        uniqueTags.some((item) => item === currentItem.name)
      )
    );
    return [liveSearch, suggested];
  }
}

export function Discover() {
  const [inputValue, setInputValue] = useState('');

  const causes = useSelector((state) => state.causes.causes);

  const recentlyCauses = causes.slice(causes.length - 5, causes.length);

  const changeInputValue = (event) =>
    setInputValue(event.target.value.toLocaleLowerCase());
  const clearInput = () => setInputValue('');

  const [modalState, setModal] = useState(false);
  const changeModalState = () => setModal(!modalState);

  const [categoriesFiltration, setCategories] = useState([]);

  const changeCategories = (item) => setCategories(item);
  const filtrationElement = useMemo(
    () => filtration(causes, inputValue, categoriesFiltration),
    [inputValue, categoriesFiltration]
  );

  return (
    <section className="section-discover">
      <SortModal
        isOpen={modalState}
        changeModalState={changeModalState}
        changeCategories={changeCategories}
      />
      <section className="section-discover-causes">
        <h3 className="section-discover-causes__title">
          {inputValue.length === 0 ? (
            'discover causes'
          ) : (
            <h3 className="section-discover-causes__search">
              <img src={back} alt="" onClick={clearInput} /> search
            </h3>
          )}
        </h3>
        <section className="section-discover-causes__filtration">
          <div className="section-discover-causes__filtration__input">
            <img src={search} alt="" />
            <input
              type="text"
              onChange={changeInputValue}
              value={inputValue}
              placeholder="Search"
            />
          </div>
          <span
            className="section-discover-causes__filtration__sort"
            onClick={changeModalState}>
            <img src={filter} alt="" />
          </span>
        </section>
      </section>
      <section className="section-discover-recently-added">
        <h3 className="section-discover-recently-added__title">
          {!!inputValue ||
            categoriesFiltration.length !== 0 ||
            'recently added:'}
        </h3>
        {inputValue ? (
          <ul className="section-discover-find__list">
            {filtrationElement[0].map((item, index) => {
              return (
                <li className="section-discover-find__list__item" key={index}>
                  <NavLink to={`/selectCause/${item.name}`}>
                    <div
                      className="content_item"
                      style={{ backgroundImage: `url(${item.image_url})` }}>
                      <img src={like} alt="" />
                      {item.name}
                    </div>
                  </NavLink>
                </li>
              );
            })}
          </ul>
        ) : (
          <ul className="section-discover-recently-added__list">
            {categoriesFiltration.length !== 0 ? (
              <></>
            ) : (
              <>
                {recentlyCauses.map((item, index) => {
                  return (
                    <li
                      className="section-discover-recently-added__list__item"
                      key={index}
                      style={{ backgroundImage: `url("${item.image_url}")` }}>
                      {item.name}
                    </li>
                  );
                })}
              </>
            )}
          </ul>
        )}
      </section>
      <section className="section-discover-discover">
        <h3 className="section-discover-discover__title">
          {!inputValue.length ? 'discover:' : 'suggested'}
        </h3>
        {inputValue ? (
          <>
            {filtrationElement[0].length !== 0 ? (
              <ul className="section-discover-discover__list">
                {filtrationElement[1].map((item, index) => {
                  return (
                    <li
                      className="section-discover-discover__list__item"
                      key={index}>
                      <NavLink to={`/selectCause/${item.name}`}>
                        <div
                          className="content_item"
                          style={{ backgroundImage: `url(${item.image_url})` }}>
                          <img src={like} alt="" />
                          {item.name}
                        </div>
                      </NavLink>
                    </li>
                  );
                })}
              </ul>
            ) : (
              <div className="section-discover-empty">
                <img src={empty_element_sort} alt="" />
                <h3 className="section-discover-empty__title">
                  Item not found
                </h3>
                <h4 className="section-discover-empty__description">
                  Try a more generic search term or try <br /> looking for
                  alternative causes
                </h4>
              </div>
            )}
          </>
        ) : (
          <ul className="section-discover-discover__list">
            {categoriesFiltration.length !== 0 ? (
              <>
                {filtrationElement[0].map((item, index) => {
                  return (
                    <li
                      className="section-discover-discover__list__item"
                      key={index}>
                      <NavLink to={`/selectCause/${item.name}`}>
                        <div
                          className="content_item"
                          style={{ backgroundImage: `url(${item.image_url})` }}>
                          <img src={like} alt="" />
                          {item.name}
                        </div>
                      </NavLink>
                    </li>
                  );
                })}
              </>
            ) : (
              <>
                {causes.map((item, index) => {
                  return (
                    <li
                      className="section-discover-discover__list__item"
                      key={index}>
                      <NavLink to={`/selectCause/${item.name}`}>
                        <div
                          className="content_item"
                          style={{ backgroundImage: `url(${item.image_url})` }}>
                          <img src={like} alt="" />
                          {item.name}
                        </div>
                      </NavLink>
                    </li>
                  );
                })}
              </>
            )}
          </ul>
        )}
      </section>
    </section>
  );
}
