export function getCauses() {
  return async (dispatch) => {
    try {
      const db = firebase.firestore();
      const causes = await db.collection('causes').get();
      const causesItems = [];
      causes.forEach((cause) => {
        causesItems.push(cause.data());
      });
      const getCauseTags = causesItems.map((items) => {
        const tags = [];
        items.tags.forEach((item) => {
          db.collection('tags')
            .doc(item)
            .get()
            .then((result) => {
              tags.push(result.data());
            });
        });

        return {
          ...items,
          tags: tags,
        };
      });
      console.log(getCauseTags);
      dispatch({
        type: GET_CAUSES,
        payload: getCauseTags,
      });
    } catch (e) {
      console.error(e);
    }
  };
}

export function getTags() {
  return async (dispatch) => {
    try {
      const db = firebase.firestore();
      const tags = await db.collection('tags').get();
      const tagsItems = [];
      tags.forEach((tag) => {
        tagsItems.push({
          ...tag.data(),
          id: tag.id,
        });
      });
      dispatch({
        type: GET_TAGS,
        payload: tagsItems,
      });
    } catch (e) {
      console.error(e);
    }
  };
}

