import ReactModal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import notificationIcon from '../../assets/images/notification-icon.png';
import { resetError, unsetVerifyEmail } from '../../redux/actions';

const LOCAL_ERRORS = ['EMAIL_NOT_VERIFIED'];

export function VerifyEmailModal({ isOpen, onClose, email, onLoginModalOpen }) {
  const dispatch = useDispatch();
  const error = useSelector((state) => state.error.error);
  const backToLoginModal = () => {
    onLoginModalOpen();
    onClose();
    dispatch(unsetVerifyEmail());
    dispatch(resetError());
  };
  const onModalClose = () => {
    onClose();
    dispatch(unsetVerifyEmail());
    if (LOCAL_ERRORS.includes(error?.code)) dispatch(resetError());
  };
  const customStyles = {
    overlay: {
      backgroundColor: 'rgba(0, 0, 0, 0.5)',
      position: 'fixed',
      height: '100%',
      width: '100%',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'flex-start',
      overflowY: 'scroll',
    },
    content: {
      minWidth: '655px',
      padding: '58px 70px',
      backgroundColor: '#272A33',
      borderRadius: '23px',
      textAlign: 'center',
      outline: 'none',
      position: 'relative',
      zIndex: 0,
      margin: 'auto 0',
    },
  };
  return (
    <ReactModal
      isOpen={isOpen}
      onRequestClose={onModalClose}
      style={customStyles}
      className="authorization-modal">
      {
        <article className="authorization-modal-content">
          <div className="authorization-modal__right">
            <p className="authorization-modal__back" onClick={backToLoginModal}>
              Back
            </p>
          </div>
          <header style={{ textAlign: 'left' }}>
            <h2 className="authorization-modal__title">
              Check email for verification
            </h2>
          </header>
          <div className="authorization-modal__notification">
            <img
              className="authorization-modal__img"
              src={notificationIcon}
              alt="notification icon"
            />
          </div>
          <p className="authorization-modal__info">
            Weve sent a verification email to:{' '}
            <span className="authorization-modal__email">{email}</span>
          </p>
          <p className="authorization-modal__info">
            Simply click the verification link in the email and your account is
            verified{' '}
          </p>
          {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
          <footer className="authorization-modal__info">
            Need help?{' '}
            <a className="authorization-modal__link">Contact support</a>
          </footer>
        </article>
      }
    </ReactModal>
  );
}
