import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useParams } from 'react-router';
import './selectCause.scss';
import back from '../../../assets/images/Back.svg';
import xc from '../../../assets/images/select_cause_back.svg';
import { NavLink } from 'react-router-dom';
import { CauseShareModal } from '../../../components/modal/modalSelected/causeShareModal';

export function SelectCause() {
  const { name } = useParams();
  const causes = useSelector((state) => state.causes.causes);
  const selectCause = causes.filter((item) => item.name === name);
  const [openShareModal, setShareModal] = useState(false);

  const changeShareModal = () => {
    setShareModal(!openShareModal);
  };
  return (
    <article className="select-cause-page">
      <CauseShareModal
        isOpen={openShareModal}
        changeShareModal={changeShareModal}
      />
      <header className="select-cause-page-header">
        <h3 className="select-cause-page-header__title">
          <NavLink to={`/discover`}>
            <img src={back} alt="" />
          </NavLink>
          {selectCause[0].name}
        </h3>
        <div className="select-cause-page-header__buttons">
          <NavLink to={`/supportSelectCause/${name}`} className="select-cause-page-header__buttons__support">
            support campaign
          </NavLink>
          <img
            className="select-cause-page-header__buttons__share"
            src={xc}
            alt=""
            onClick={() => changeShareModal()}
          />
        </div>
      </header>
      <main className="select-cause-page-main">
        <img src={selectCause[0].image_url} alt="" />
      </main>
      <footer className="select-cause-page-footer">
        <div className="select-cause-page-footer-container">
          <h3 className="select-cause-page-footer-container__title">
            {selectCause[0].tagline}
          </h3>
          <p className="select-cause-page-footer-container__description">
            {selectCause[0].description}
          </p>
        </div>
      </footer>
    </article>
  );
}
